
.legend {
  text-align: left;
  line-height: 20px;
  color: #555;
  bottom: 25px;
  right: 16px;
  position: absolute;
}

.legend i {
  width: 12px;
  height: 12px;
  float: left;
  margin-right: 8px;
  opacity: 0.7;
  border-radius: 18px;
  margin-top: 4px;
}

.info {
  padding: 6px 8px;
  font: 14px/16px Arial, Helvetica, sans-serif;
  background: white;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.leaflet-control {
  margin-right:0px !important;
}

.info h4 {
  margin: 0 0 5px;
  color: #777;
}

#geolegend {
  width: 85px;
}
